import React, { useEffect, useState } from 'react';
import emailjs from '@emailjs/browser';
import '../styles/products.scss';
import { Link } from 'react-router-dom';
import { FaShoppingCart } from 'react-icons/fa';
import { useTranslation } from 'react-i18next';

const Products = () => {
  const { t } = useTranslation();
  const [searchTerm, setSearchTerm] = useState('');
  const [modalProduct, setModalProduct] = useState(null);
  const [formData, setFormData] = useState({ color: '', size: '', quantity: 1, name: '', address: '', phone: '' });

  const PRODUCTS = t('products.items', { returnObjects: true }).map((item, index) => ({
    id: index + 1,
    name: item.name,
    price: [45900, 150000, 250000, 120000, 80000, 100000, 200000, 250000, 500000, 50000][index],
    colors: item.colors,
    image: require(`../assets/images/products/product${index + 1}.png`)
  }));

  useEffect(() => {
    emailjs.init('ucpvNgWaQb5t7FmhM');
  }, []);

  const handleBuyNow = (product) => {
    setModalProduct(product);
  };

  const handleFormSubmit = (e) => {
    e.preventDefault();
    const { color, size, quantity, name, address, phone } = formData;
    if (!color || !size || quantity < 1) {
      alert(t('products.modal.validationMessage'));
      return;
    }

    emailjs
      .send('dulichxanhastral', 'template_j7ah4od', {
        product_name: modalProduct.name,
        product_price: modalProduct.price.toLocaleString('vi-VN') + ' VND',
        product_size: size,
        product_color: color,
        quantity_input: quantity,
        buyer_name: name,
        buyer_phone: phone,
        buyer_address: address,
      })
      .then(() => {
        alert(t('products.modal.successMessage'));
        setModalProduct(null);
        setFormData({ color: '', size: '', quantity: 1, name: '', address: '', phone: '' });
      })
      .catch((error) => {
        console.error('Email sending error:', error);
        alert(t('products.modal.errorMessage'));
      });
  };

  const filteredProducts = PRODUCTS.filter((p) => 
    p.name.toLowerCase().includes(searchTerm.toLowerCase())
  );

  return (
    <div className="products-page">
      <video autoPlay muted loop id="background-video">
        <source src="/img/0513.mp4" type="video/mp4" />
        {t('videoNotSupported')}
      </video>

      <div className="search-bar">
        <input
          type="text"
          id="search-input"
          placeholder={t('products.searchPlaceholder')}
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
        />
      </div>

      <div className="product-grid">
        {filteredProducts.map((product) => (
          <div key={product.id} className="item">
            <div className="image-box">
              <img src={product.image} alt={product.name} />
            </div>
            <div className="details">
              <h2>{product.name}</h2>
              <div className="buttons">
                <Link to={`/product-detail/${product.id}`} className="btn btn-info">
                  {t('products.viewDetails')}
                </Link>
                <button className="btn btn-buy" onClick={() => handleBuyNow(product)}>
                  {t('products.buyNow')}
                </button>
              </div>
            </div>
          </div>
        ))}
      </div>

      {modalProduct && (
        <div className="modal fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50" onClick={() => setModalProduct(null)}>
          <div className="modal-content bg-white rounded-lg p-6 w-full max-w-md" onClick={(e) => e.stopPropagation()}>
            <span className="close-button absolute top-4 right-4 text-2xl cursor-pointer hover:text-gray-600" onClick={() => setModalProduct(null)}>
              &times;
            </span>

            <h2 className="text-xl font-bold mb-4">{t('products.modal.title')}</h2>
            <p id="current-price" className="text-lg font-semibold mb-6">
              {t('products.modal.price', { price: modalProduct.price.toLocaleString('vi-VN') })}
            </p>

            <form onSubmit={handleFormSubmit} className="buy-now-form space-y-4">
              <div className="cart-input-group">
                <label className="block mb-2 font-medium">{t('products.modal.colorLabel')}</label>
                <select
                  className="cart-input w-full p-2 border rounded"
                  value={formData.color}
                  onChange={(e) => setFormData({ ...formData, color: e.target.value })}
                  required
                >
                  <option value="">{t('products.modal.colorPlaceholder')}</option>
                  {modalProduct.colors.map((c) => (
                    <option key={c} value={c}>{c}</option>
                  ))}
                </select>
              </div>

              <div className="cart-input-group">
                <label className="block mb-2 font-medium">{t('products.modal.sizeLabel')}</label>
                <select
                  className="cart-input w-full p-2 border rounded"
                  value={formData.size}
                  onChange={(e) => setFormData({ ...formData, size: e.target.value })}
                  required
                >
                  <option value="">{t('products.modal.sizePlaceholder')}</option>
                  {t('products.sizes', { returnObjects: true }).map((size) => (
                    <option key={size} value={size}>{size}</option>
                  ))}
                </select>
              </div>

              <div className="cart-input-group">
                <label className="block mb-2 font-medium">{t('products.modal.quantityLabel')}</label>
                <input
                  className="cart-input w-full p-2 border rounded"
                  type="number"
                  min="1"
                  value={formData.quantity}
                  onChange={(e) => setFormData({ ...formData, quantity: e.target.value })}
                  required
                />
              </div>

              <div className="cart-input-group">
                <label className="cart-label block mb-2 font-medium">{t('products.modal.nameLabel')}</label>
                <input
                  className="cart-input w-full p-2 border rounded"
                  type="text"
                  value={formData.name}
                  placeholder={t('products.modal.namePlaceholder')}
                  onChange={(e) => setFormData({ ...formData, name: e.target.value })}
                  required
                />
              </div>

              <div className="cart-input-group">
                <label className="cart-label block mb-2 font-medium">{t('products.modal.addressLabel')}</label>
                <input
                  className="cart-input w-full p-2 border rounded"
                  type="text"
                  value={formData.address}
                  placeholder={t('products.modal.addressPlaceholder')}
                  onChange={(e) => setFormData({ ...formData, address: e.target.value })}
                  required
                />
              </div>

              <div className="cart-input-group">
                <label className="cart-label block mb-2 font-medium">{t('products.modal.phoneLabel')}</label>
                <input
                  className="cart-input w-full p-2 border rounded"
                  type="tel"
                  pattern="0[0-9]{9}"
                  value={formData.phone}
                  placeholder={t('products.modal.phonePlaceholder')}
                  onChange={(e) => setFormData({ ...formData, phone: e.target.value })}
                  required
                />
              </div>

              <button
                type="submit"
                className="w-full py-2 px-4 rounded font-medium transition-colors"
              >
                {t('products.modal.submitButton')}
              </button>
            </form>
          </div>
        </div>
      )}

      <Link to="/cart" className="cart-button">
        <FaShoppingCart />
      </Link>
    </div>
  );
};

export default Products;