import React, { useEffect, useState } from 'react';
import emailjs from '@emailjs/browser';
import '../styles/Cart.scss';
import { useTranslation } from 'react-i18next';

const Cart = () => {
  const { t } = useTranslation();
  const [cart, setCart] = useState([]);
  const [selectedProductIndex, setSelectedProductIndex] = useState(null);
  const [modalOpen, setModalOpen] = useState(false);
  const [buyerInfo, setBuyerInfo] = useState({ name: '', address: '', phone: '' });

  const formatPrice = (price) =>
    price.toLocaleString('vi-VN', { style: 'currency', currency: 'VND' });

  useEffect(() => {
    const savedCart = JSON.parse(localStorage.getItem('cart')) || [];
    const parsedCart = savedCart.map(item => ({
      ...item,
      price: typeof item.price === 'string' ? parseInt(item.price.replace(/[₫]/g, ''), 10) : item.price,
      quantity: item.quantity || 1,
    }));
    setCart(parsedCart);
    emailjs.init('ucpvNgWaQb5t7FmhM');
  }, []);

  const updateCart = (newCart) => {
    setCart(newCart);
    localStorage.setItem('cart', JSON.stringify(newCart));
  };

  const handleQuantityChange = (index, value) => {
    const newCart = [...cart];
    newCart[index].quantity = Math.max(1, parseInt(value) || 1);
    updateCart(newCart);
  };

  const handleRemove = (index) => {
    const newCart = cart.filter((_, i) => i !== index);
    updateCart(newCart);
  };

  const handleBuyOne = (index) => {
    setSelectedProductIndex(index);
    setModalOpen(true);
  };

  const handleBuyAll = () => {
    if (cart.length === 0) {
      alert(t('cart.messages.emptyCart'));
      return;
    }
    setSelectedProductIndex(null);
    setModalOpen(true);
  };

  const getColorCode = (name) => {
    const colors = t('cart.colorCodes', { returnObjects: true });
    return colors[name] || '#000';
  };

  const handleSubmitOrder = (e) => {
    e.preventDefault();

    let productsToOrder;
    let newCart;

    if (selectedProductIndex === null) {
      productsToOrder = [...cart];
      newCart = [];
    } else {
      productsToOrder = [cart[selectedProductIndex]];
      newCart = cart.filter((_, idx) => idx !== selectedProductIndex);
    }

    const productDetails = productsToOrder.map(item =>
      `- ${t('productDetail.nameLabel')}: ${item.name}\n` +
      `- ${t('productDetail.priceLabel')}: ${item.price}\n` +
      `- ${t('productDetail.sizeLabel')}: ${item.size}\n` +
      `- ${t('productDetail.colorLabel')}: ${item.color}\n` +
      `- ${t('productDetail.quantityLabel')}: ${item.quantity}\n`
    ).join('\n');

    emailjs
      .send('dulichxanhastral', 'template_3nnken4', {
        buyer_name: buyerInfo.name,
        buyer_address: buyerInfo.address,
        buyer_phone: buyerInfo.phone,
        product_details: productDetails,
      })
      .then(() => {
        alert(t('cart.messages.orderSuccess'));
        setCart(newCart);
        localStorage.setItem('cart', JSON.stringify(newCart));
        setModalOpen(false);
      })
      .catch((error) => {
        console.error('Email error:', error);
        alert(t('cart.messages.orderError'));
      });
  };

  return (
    <div className="cart-container">
      <h1>{t('cart.title')}</h1>
      <table className="cart-table">
        <thead>
          <tr>
            <th>{t('cart.tableHeaders.image')}</th>
            <th>{t('cart.tableHeaders.product')}</th>
            <th>{t('cart.tableHeaders.variant')}</th>
            <th>{t('cart.tableHeaders.price')}</th>
            <th>{t('cart.tableHeaders.quantity')}</th>
            <th>{t('cart.tableHeaders.total')}</th>
            <th>{t('cart.tableHeaders.buy')}</th>
            <th>{t('cart.tableHeaders.remove')}</th>
          </tr>
        </thead>
        <tbody>
          {cart.map((item, index) => (
            <tr key={index}>
              <td>
                <img
                  src={item.image}
                  alt={item.name}
                  style={{ width: '50px', height: '50px', objectFit: 'cover' }}
                />
              </td>
              <td>{item.name}</td>
              <td>
                {t('cart.variantFormat', { size: item.size })}
                <span
                  style={{
                    backgroundColor: getColorCode(item.color),
                    width: 20,
                    height: 20,
                    display: 'inline-block',
                    borderRadius: '50%',
                    border: '1px solid #ccc',
                    marginLeft: 5,
                    verticalAlign: 'middle',
                    marginBottom: 5
                  }}
                ></span>
              </td>
              <td>{formatPrice(item.price)}</td>
              <td>
                <input
                  type="number"
                  min="1"
                  value={item.quantity}
                  onChange={(e) => handleQuantityChange(index, e.target.value)}
                />
              </td>
              <td>{formatPrice(item.price * item.quantity)}</td>
              <td>
                <button onClick={() => handleBuyOne(index)} className="buy-button">
                  {t('cart.buttons.buy')}
                </button>
              </td>
              <td>
                <button onClick={() => handleRemove(index)} className="remove-button">
                  {t('cart.buttons.remove')}
                </button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>

      <div className="cart-summary">
        <button onClick={handleBuyAll} id="checkout-all">
          {t('cart.buttons.buyAll')}
        </button>
      </div>

      {modalOpen && (
        <div
          className="modal-cart-container fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4"
          onClick={() => setModalOpen(false)}
        >
          <div
            className="modal-cart-content rounded-lg shadow-xl w-full max-w-md overflow-hidden"
            onClick={(e) => e.stopPropagation()}
          >
            <div className="flex justify-between items-center p-4 border-b">
              <h2 className="text-xl font-bold">{t('cart.modal.title')}</h2>
              <button
                className="text-gray-500 hover:text-gray-700 text-2xl"
                onClick={() => setModalOpen(false)}
              >
                &times;
              </button>
            </div>

            <form onSubmit={handleSubmitOrder} className="p-6">
              <div className="mb-4">
                <label className="cart-label block text-sm font-medium mb-2">
                  {t('cart.modal.nameLabel')}
                </label>
                <input
                  type="text"
                  required
                  className="cart-input w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                  value={buyerInfo.name}
                  placeholder={t('cart.modal.namePlaceholder')}
                  onChange={(e) => setBuyerInfo({ ...buyerInfo, name: e.target.value })}
                />
              </div>

              <div className="mb-4">
                <label className="cart-label block text-sm font-medium mb-2">
                  {t('cart.modal.addressLabel')}
                </label>
                <input
                  type="text"
                  required
                  className="cart-input w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                  value={buyerInfo.address}
                  placeholder={t('cart.modal.addressPlaceholder')}
                  onChange={(e) => setBuyerInfo({ ...buyerInfo, address: e.target.value })}
                />
              </div>

              <div className="mb-6">
                <label className="cart-label block text-sm font-medium mb-2">
                  {t('cart.modal.phoneLabel')}
                </label>
                <input
                  type="tel"
                  pattern="0[0-9]{9}"
                  required
                  className="cart-input w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                  value={buyerInfo.phone}
                  placeholder={t('cart.modal.phonePlaceholder')}
                  onChange={(e) => setBuyerInfo({ ...buyerInfo, phone: e.target.value })}
                />
                <p className="text-xs text-gray-500 mt-1">{t('cart.modal.phoneExample')}</p>
              </div>

              <button
                type="submit"
                className="w-full bg-blue-600 hover:bg-blue-700 text-white font-medium py-2 px-4 rounded-md transition duration-300"
              >
                {t('cart.modal.submitButton')}
              </button>
            </form>
          </div>
        </div>
      )}
    </div>
  );
};

export default Cart;