import React, { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import emailjs from '@emailjs/browser';
import { useTranslation } from 'react-i18next';
import '../styles/productDetail.scss'; // Thêm CSS cho trang chi tiết sản phẩm

const productImages = [
  require('../assets/images/products/product1.png'),
  require('../assets/images/products/product1-1.png'),
  require('../assets/images/products/product1-2.png'),
  require('../assets/images/products/product2.png'),
  require('../assets/images/products/product2-1.png'),
  require('../assets/images/products/product2-2.png'),
  require('../assets/images/products/product3.png'),
  require('../assets/images/products/product3-1.png'),
  require('../assets/images/products/product3-2.png'),
  require('../assets/images/products/product4.png'),
  require('../assets/images/products/product4-1.png'),
  require('../assets/images/products/product4-2.png'),
  require('../assets/images/products/product5.png'),
  require('../assets/images/products/product5-1.png'),
  require('../assets/images/products/product5-2.png'),
  require('../assets/images/products/product6.png'),
  require('../assets/images/products/product6-1.png'),
  require('../assets/images/products/product6-2.png'),
  require('../assets/images/products/product7.png'),
  require('../assets/images/products/product7-1.png'),
  require('../assets/images/products/product7-2.png'),
  require('../assets/images/products/product8.png'),
  require('../assets/images/products/product8-1.png'),
  require('../assets/images/products/product8-2.png'),
  require('../assets/images/products/product9.png'),
  require('../assets/images/products/product9-1.png'),
  require('../assets/images/products/product9-2.png'),
  require('../assets/images/products/product10.png'),
  require('../assets/images/products/product10-1.png'),
  require('../assets/images/products/product10-2.png')
];

const ProductDetail = () => {
  const { t } = useTranslation();
  const { id } = useParams();

  // Get product data from translation file
  const productData = t('productDetail.products', { returnObjects: true }).map((product, index) => ({
    id: (index + 1).toString(),
    name: product.name,
    price: {
      current: `₫${[45000, 150000, 250000, 120000, 80000, 100000, 200000, 250000, 500000, 50000][index].toLocaleString('vi-VN')}`,
      old: `₫${[90000, 200000, 300000, 150000, 100000, 150000, 250000, 300000, 600000, 70000][index].toLocaleString('vi-VN')}`,
      discount: `-${[50, 25, 17, 20, 20, 14, 20, 17, 17, 17][index]}%`
    },
    description: product.description,
    images: productImages.slice(index * 3, index * 3 + 3),
    options: { colors: product.colors }
  }));

  const product = productData.find(p => p.id === id);
  const [selectedColor, setSelectedColor] = useState('');
  const [selectedSize, setSelectedSize] = useState('');
  const [quantity, setQuantity] = useState(1);
  const [mainImage, setMainImage] = useState(product?.images?.[0] || '');
  const [modalOpen, setModalOpen] = useState(false);
  const [buyerInfo, setBuyerInfo] = useState({ name: '', address: '', phone: '' });

  useEffect(() => {
    emailjs.init('ucpvNgWaQb5t7FmhM');
  }, []);

  if (!product) return <p style={{ textAlign: 'center' }}>{t('productDetail.notFound')}</p>;

  const handleAddToCart = () => {
    if (!selectedColor || !selectedSize) {
      alert(t('productDetail.validation.colorSizeRequired'));
      return;
    }

    const cartItem = {
      name: product.name,
      price: Number(product.price.current.replace(/[₫\.\,]/g, '')),
      image: mainImage,
      color: selectedColor,
      size: selectedSize,
      quantity,
    };

    const cart = JSON.parse(localStorage.getItem('cart')) || [];
    cart.push(cartItem);
    localStorage.setItem('cart', JSON.stringify(cart));

    alert(t('productDetail.messages.addedToCart'));
  };

  const handleBuyNow = () => {
    if (!selectedColor || !selectedSize || quantity <= 0) {
      alert(t('productDetail.validation.infoRequired'));
      return;
    }
    setModalOpen(true);
  };

  const handleSubmitOrder = (e) => {
    e.preventDefault();
    emailjs
      .send('dulichxanhastral', 'template_j7ah4od', {
        product_name: product.name,
        product_price: product.price.current,
        product_color: selectedColor,
        product_size: selectedSize,
        product_quantity: quantity,
        buyer_name: buyerInfo.name,
        buyer_address: buyerInfo.address,
        buyer_phone: buyerInfo.phone
      })
      .then(() => {
        alert(t('productDetail.messages.orderSuccess'));
        setModalOpen(false);
      })
      .catch((error) => {
        console.error('Email error:', error);
        alert(t('productDetail.messages.orderError'));
      });
  };

  return (
    <div className="product-detail">
      {/* Product Images Section */}
      <div className="product-images">
        <div className="main-image">
          <img src={mainImage} alt={product.name} />
        </div>
        <div className="image-thumbnails">
          {product.images.map((img, index) => (
            <img
              key={index}
              className="thumbnail"
              src={img}
              alt={`${product.name} ${index + 1}`}
              onClick={() => setMainImage(img)}
            />
          ))}
        </div>
      </div>

      {/* Product Info Section */}
      <div className="product-info">
        <h1>{product.name}</h1>
        <p className="product-description">{product.description}</p>

        <div className="product-price">
          <span className="current-price">{product.price.current}</span>
          <span className="old-price">{product.price.old}</span>
          <span className="discount">{product.price.discount}</span>
        </div>

        <div className="product-options">
          <div className="option">
            <label>{t('productDetail.colorLabel')}</label>
            <div className="choices">
              {product.options.colors.map((color, index) => (
                <button
                  key={index}
                  className={`choice color ${selectedColor === color ? 'selected' : ''}`}
                  onClick={() => setSelectedColor(color)}
                >
                  {color}
                </button>
              ))}
            </div>
          </div>

          <div className="option">
            <label>{t('productDetail.sizeLabel')}</label>
            <div className="choices">
              {t('productDetail.sizes', { returnObjects: true }).map((size) => (
                <button
                  key={size}
                  className={`choice size ${selectedSize === size ? 'selected' : ''}`}
                  onClick={() => setSelectedSize(size)}
                >
                  {size}
                </button>
              ))}
            </div>
          </div>
        </div>

        <div className="quantity">
          <label>{t('productDetail.quantityLabel')}</label>
          <div className="quantity-control">
            <button onClick={() => setQuantity(Math.max(1, quantity - 1))}>-</button>
            <input
              type="number"
              value={quantity}
              onChange={(e) => setQuantity(Math.max(1, parseInt(e.target.value) || 1))}
            />
            <button onClick={() => setQuantity(quantity + 1)}>+</button>
          </div>
        </div>

        <div className="action-buttons">
          <button className="add-cart" onClick={handleAddToCart}>
            {t('productDetail.addToCart')}
          </button>
          <button className="buy-now" onClick={handleBuyNow}>
            {t('productDetail.buyNow')}
          </button>
        </div>
      </div>

      {/* Order Modal */}
      {modalOpen && (
        <div className="modal-product-detail-container" onClick={() => setModalOpen(false)}>
          <div className="modal-product-detail-content" onClick={(e) => e.stopPropagation()}>
            <div className="modal-header">
              <button onClick={() => setModalOpen(false)}>&times;</button>
              <h2>{t('productDetail.modal.title')}</h2>
            </div>

            <form onSubmit={handleSubmitOrder} className="modal-body">
              <div className="card-input-group">
                <label className="cart-label">
                  {t('productDetail.modal.nameLabel')} <span className="text-red-500">{t('productDetail.modal.requiredField')}</span>
                </label>
                <input
                  type="text"
                  required
                  className="cart-input"
                  value={buyerInfo.name}
                  placeholder={t('productDetail.modal.namePlaceholder')}
                  onChange={(e) => setBuyerInfo({ ...buyerInfo, name: e.target.value })}
                />
              </div>

              <div>
                <label className="cart-label">
                  {t('productDetail.modal.addressLabel')} <span className="text-red-500">{t('productDetail.modal.requiredField')}</span>
                </label>
                <input
                  type="text"
                  required
                  className="cart-input"
                  value={buyerInfo.address}
                  placeholder={t('productDetail.modal.addressPlaceholder')}
                  onChange={(e) => setBuyerInfo({ ...buyerInfo, address: e.target.value })}
                />
              </div>

              <div>
                <label className="cart-label">
                  {t('productDetail.modal.phoneLabel')} <span className="text-red-500">{t('productDetail.modal.requiredField')}</span>
                </label>
                <input
                  type="tel"
                  pattern="0[0-9]{9}"
                  required
                  className="cart-input"
                  value={buyerInfo.phone}
                  placeholder={t('productDetail.modal.phonePlaceholder')}
                  onChange={(e) => setBuyerInfo({ ...buyerInfo, phone: e.target.value })}
                />
                <p className="text-xs text-gray-500">{t('productDetail.validation.phoneFormat')}</p>
              </div>

              <div className="pt-4">
                <button type="submit" className="w-full bg-gradient-to-r from-blue-600 to-blue-500">
                  {t('productDetail.modal.submitButton')}
                </button>
              </div>
            </form>
          </div>
        </div>
      )}
    </div>
  );
};

export default ProductDetail;